@import url(https://fonts.googleapis.com/css2?family=Domine:wght@400;700&display=swap);
@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.3.0/font/bootstrap-icons.css);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7ebf0;
}

* {
  font-family: 'Domine', serif;
}

div {
  color: black;
}

::-webkit-calendar-picker-indicator {
  filter: invert(1);
}
:root {
  --bar: #1976d2;
  --bg: #e7ebf0;
  --secondaryBg: #1f2c34;
  --toastBg: #6d6c6c71;
  --green: #005c4b;
}

body {
  background-color: (--bg);  
  color: white;
  font-size: 4vw;
}

input {
  outline: 0;
  border-width: 0 0 0.1rem;
  background-color: rgba(0, 0, 0, 0);
  color: white;
  font-size: 4vw;
}

button {
  background-color: #005c4b;
  background-color: var(--green);
  color: white;
  outline: none;
  border: none;
  cursor: pointer;
  font-size: 5vw;
  padding: 2%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: background-color 0.5s;
}

button:disabled {
  opacity: 0.5;
}

input:focus {
  border-color: #005c4b;
  border-color: var(--green)
}

i {
  color:#00a356;
  font-size: 4vw;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  margin: 0 2vw 0 .75vw;
}

.App {
  display: grid;
  grid-template-areas:
  'header'
  'body'
}

.glass {
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  z-index: 99;
}

.dark-glass {
  background-color: black;
  opacity: 0.75;
}

.clickable {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

span.ripple {
  position: absolute; /* The absolute position we mentioned earlier */
  border-radius: 50%;
  transform: scale(0);
  animation: ripple 600ms linear;
  background-color: rgba(255, 255, 255, 0.7);
}

.unsel {
  -webkit-user-select: none; /* Safari */ /* Firefox */
  -ms-user-select: none; /* IE10+/Edge */
  user-select: none; /* Standard */
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}

.light {
  color: #555;
}


.vol {
    display: flex;
    justify-content: center;
    margin: 4vw 0 0 0;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
}
.mainMenu {
    position: fixed;
    z-index: 100;
    left: 0;
    width: 70%;
    height: 100vh;
    background-color: var(--secondaryBg);
    transition: left .3s ease-in-out, opacity .3s ease-in-out;
    box-shadow: #0005 2px 2px 10px;
}

.mainMenuItem {
    color: white;
    padding: 3vw;
}

.mainMenu#show {
    left: 0px
}

.mainMenu#hide {
    left: -80vw;
}

.mainMenuProfile {
    color: white;
    display: grid;
    justify-items: center;
}

.mainMenuProfileAvatar {
    display: grid;
    background: cadetblue;
    font-size: 8vw;
    font-weight: 400;
    border-radius: 50%;
    width: 10vw;
    height: 10vw;
    align-content: center;
    justify-content: center;
    margin: 3vw;
}

.mainMenuProfileName {
    font-size: 5vw;
    font-weight: 400;
}



.header_logo {
    width:12vw;
}

.appHeader {
    position: relative;
    display: flex;
    height: 10vw;
    width: 100%;
    background-color: var(--bar);
    align-items: center;
    justify-content: space-around;
    color: white;
    font-size: 4.3vw;
}

#menuButton {
    margin: 0% 3% 0% 0%;
    font-size: 4vw;
}

#slideMenuButton {
    margin: 0% 0% 0% 3%;
}

.clickableIcon {
    display: flex;
    width: 6vw;
    height: 6vw;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
    border-radius: 50%;
}
.paper {
    background-color: white;
    padding: 3vw;
    width: 80vw;
    border-radius: 1vw;
    box-shadow: -2px 2px 3px #aaa;
}
.tab {
    width: 100%;
}

.tabHeaderItems {
    display: flex;
    justify-content: space-around;
    height: 8vw;
    align-items: center;
}

.tabHeaderItem {
    width: 100%;
    text-align: center;
    cursor: pointer;
}


.tabUnderline {
    position: relative;
    height: 0.5vw;
    background: var(--bar);
    transition: left .2s ease-in-out;
}

.tabHeader {
    background-color: white;
}
.service {
    padding: 2vw;
}

.detailsDiv {
    margin-bottom: 3vw;
}

.detailFeild{
    padding: .5vw 0 0 0;
    font-size: 4vw;
}
.hsep {
    border: #0000003b 1px solid;
}
.spinner-bg-circle {
    border: 0.75vw solid rgba(128, 128, 128, 0.397);
    border-top: 0.75vw solid var(--bar);
    border-right: 0.75vw solid var(--bar);
    border-bottom: 0.75vw solid var(--bar);
    width: 15px;
    height: 15px;
    border-radius: 50%;
    animation: spinner 1s ease-in-out infinite;
    transition: opacity 0.5s;
    opacity: 1;
}

.spinner-show {
 opacity: 1;
}

.spinner-hide {
 opacity: 0;
}

@keyframes spinner {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}

.autoIn {
    background-color: white;
    border: none;
}

.autoInHolder {
    display: flex;
    height: 100%;
    align-items: center;
}

.autoInRoot {
    height: 7vw;
}
.ser {
    display: flex;
    justify-content: center;
    margin: 4vw 0 0 0;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
}

.serServ{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 90vh;
    overflow: scroll;
}

.svHolder {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.servIndiv {
    margin: 2vw;
    width: 100vw;
}

.serviceDetails {
    color: #555;
    font-size: 3.5vw;
    margin: 1vw;
}

.spocPhoneDet {
    display: flex;
    color: #555;
}
.volsIndiv {
    width: 100vw;
}

.eachVolDet {
    display: flex;
    justify-content: space-between;
    margin: 2vw 0 2vw 0vw;
}
