.tab {
    width: 100%;
}

.tabHeaderItems {
    display: flex;
    justify-content: space-around;
    height: 8vw;
    align-items: center;
}

.tabHeaderItem {
    width: 100%;
    text-align: center;
    cursor: pointer;
}


.tabUnderline {
    position: relative;
    height: 0.5vw;
    background: var(--bar);
    transition: left .2s ease-in-out;
}

.tabHeader {
    background-color: white;
}